<template>
  <div class="rose-echarts-container">
    <div id="roseEcharts" style="width:100%; height:100%"></div>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  created() {},
  mounted() {
    this.drawroseEcharts();
  },
  methods: {
    drawroseEcharts() {
      let myChart = this.$echarts.init(document.getElementById("roseEcharts"));
      var labelV = {
        position: "inner",
        fontSize: 8,
        align: "right",
        padding: 4
      };
      myChart.setOption(
        {
          tooltip: {
            trigger: "item",
            formatter: "{b}: {c} 万元"
          },
          series: [
            {
              type: "pie",
              clockWise: false,
              radius: ["30%", "90%"],
              center: ["50%", "60%"],
              //  startAngle: 150,
              roseType: "area",

              itemStyle: {
                normal: {
                  color: function(params) {
                    var colorList = [
                      "#d34c57",
                      "#fb8d37",
                      "#01866E",
                      "#2ED0EB",
                      "#399EE2",
                      "#3C6FE6",
                      "#4345E7",
                      "#3233E3",
                      "#0A0D9A",
                      "#5C10BE",
                      "#740BEE"
                    ];
                    return colorList[params.dataIndex];
                  }
                }
              },
              data: [
                {
                  value: 100,
                  name: "昆明市",
                  label: labelV
                //   label: {
                //     normal: {
                //       formatter: "{b}\n{c}万元",
                //       padding: 4,
                //       color: "#fff"
                //     },
                //     textStyle: {
                //       rich: {
                //         name: {
                //           fontSize: 20
                //         },
                //         num: {
                //           fontSize: 20
                //         }
                //       }
                //     }
                //   }
                },
                {
                  value: 95,
                  name: "曲靖市",
                  label: labelV
                },
                {
                  value: 90,
                  name: "玉溪市",
                  label: labelV
                },
                {
                  value: 85,
                  name: "保山市",
                  label: labelV
                },
                {
                  value: 80,
                  name: "昭通市",
                  label: labelV
                },
                {
                  value: 75,
                  name: "丽江市",
                  label: labelV
                },
                {
                  value: 70,
                  name: "普洱市",
                  label: labelV
                },
                {
                  value: 65,
                  name: "临沧市",
                  label: labelV
                },
                {
                  value: 60,
                  name: "楚雄州",
                  label: labelV
                },
                {
                  value: 55,
                  name: "红河州",
                  label: labelV
                },
                {
                  value: 50,
                  name: "文山州",
                  label: labelV
                },
                {
                  value: 45,
                  name: "西双版纳",
                  label: labelV
                },
                {
                  value: 40,
                  name: "大理州",
                  label: labelV
                },
                {
                  value: 35,
                  name: "德宏州",
                  label: labelV
                },
                {
                  value: 30,
                  name: "怒江州",
                  label: labelV
                },
                {
                  value: 25,
                  name: "迪庆州",
                  label: labelV
                }
              ]
            }
          ]
        },
        true
      );
    }
  }
};
</script>

<style scoped lang="scss">
.rose-echarts-container {
  width: 100%;
  height: 100%;
}
</style>
