<template>
  <div class="relation-container">
    <div id="relationEcharts" style="width:100%; height:100%"></div>
  </div>
</template>

<script>
import echarts from "echarts";
import yunnanJson from "echarts/map/json/province/yunnan.json";
export default {
  data() {
    return {};
  },
  created() {},
  mounted() {
    this.drawMap();
  },
  methods: {
    drawMap() {
      
      let myChart = echarts.init(document.getElementById("relationEcharts"));
      echarts.registerMap("yunnan", yunnanJson);

      var chinaGeoCoordMap = {
        昆明市: [102.712251, 25.040609],
        曲靖市: [103.797851, 25.501557],
        玉溪市: [102.543907, 24.350461],
        保山市: [99.167133, 25.111802],
        昭通市: [103.717216, 27.336999],
        丽江市: [100.233026, 26.872108],
        普洱市: [100.972344, 22.777321],
        临沧市: [100.08697, 23.886567],
        楚雄州: [101.546046, 25.041988],
        红河州: [103.384182, 23.366775],
        文山州: [104.24401, 23.38],
        西双版纳: [100.797941, 22.001724],
        大理州: [100.225668, 25.589449],
        德宏州: [98.578363, 24.436694],
        怒江州: [98.854304, 25.850949],
        迪庆州: [99.706463, 27.826853]
      };
      var chinaDatas = [
        [
          {
            name: "昆明市",
            value: 0
          }
        ],
        [
          {
            name: "玉溪市",
            value: 0
          }
        ],
        [
          {
            name: "保山市",
            value: 0
          }
        ],
        [
          {
            name: "昭通市",
            value: 0
          }
        ],
        [
          {
            name: "丽江市",
            value: 0
          }
        ],
        [
          {
            name: "普洱市",
            value: 0
          }
        ],
        [
          {
            name: "临沧市",
            value: 0
          }
        ],
        [
          {
            name: "楚雄州",
            value: 0
          }
        ],
        [
          {
            name: "红河州",
            value: 0
          }
        ],
        [
          {
            name: "文山州",
            value: 0
          }
        ],
        [
          {
            name: "西双版纳",
            value: 0
          }
        ],
        [
          {
            name: "大理州",
            value: 0
          }
        ],
        [
          {
            name: "德宏州",
            value: 0
          }
        ],
        [
          {
            name: "怒江州",
            value: 0
          }
        ],
        [
          {
            name: "迪庆州",
            value: 0
          }
        ]
      ];

      var convertData = function(data) {
        var res = [];
        for (var i = 0; i < data.length; i++) {
          var dataItem = data[i];
          var fromCoord = chinaGeoCoordMap[dataItem[0].name];
          var toCoord = [102.71, 25.04];
          if (fromCoord && toCoord) {
            res.push([
              {
                coord: fromCoord,
                value: dataItem[0].value
              },
              {
                coord: toCoord
              }
            ]);
          }
        }
        return res;
      };
      var series = [];
      [["昆明市", chinaDatas]].forEach(function(item, i) {
        console.log(item);
        series.push(
          {
            type: "lines",
            zlevel: 2,
            effect: {
              show: true,
              period: 4, //箭头指向速度，值越小速度越快
              trailLength: 0.02, //特效尾迹长度[0,1]值越大，尾迹越长重
              symbol: "arrow", //箭头图标
              symbolSize: 5 //图标大小
            },
            lineStyle: {
              normal: {
                width: 1, //尾迹线条宽度
                opacity: 1, //尾迹线条透明度
                curveness: 0.3 //尾迹线条曲直度
              }
            },
            data: convertData(item[1])
          },
          {
            type: "effectScatter",
            coordinateSystem: "geo",
            zlevel: 2,
            rippleEffect: {
              //涟漪特效
              period: 4, //动画时间，值越小速度越快
              brushType: "stroke", //波纹绘制方式 stroke, fill
              scale: 4 //波纹圆环最大限制，值越大波纹越大
            },
            label: {
              normal: {
                show: true,
                position: "right", //显示位置
                offset: [5, 0], //偏移设置
                formatter: function(params) {
                  //圆环显示文字
                  return params.data.name;
                },
                fontSize: 13
              },
              emphasis: {
                show: true
              }
            },
            symbol: "circle",
            symbolSize: function(val) {
              return 5 + val[2] * 5; //圆环大小
            },
            itemStyle: {
              normal: {
                show: false,
                color: "#f00"
              }
            },
            data: item[1].map(function(dataItem) {
              return {
                name: dataItem[0].name,
                value: chinaGeoCoordMap[dataItem[0].name].concat([
                  dataItem[0].value
                ])
              };
            })
          },
          //被攻击点
          {
            type: "scatter",
            coordinateSystem: "geo",
            zlevel: 2,
            rippleEffect: {
              period: 4,
              brushType: "stroke",
              scale: 4
            },
            label: {
              normal: {
                show: true,
                position: "right",
                //offset:[5, 0],
                color: "#0f0",
                formatter: "{b}",
                textStyle: {
                  color: "#0f0"
                }
              },
              emphasis: {
                show: true,
                color: "#f60"
              }
            },
            symbol: "pin",
            symbolSize: 50,
            data: [
              {
                name: item[0],
                value: chinaGeoCoordMap[item[0]].concat([10])
              }
            ]
          }
        );
      });

      let option = {
        tooltip: {
          trigger: "item",

          borderColor: "#FFFFCC",
          showDelay: 0,
          hideDelay: 0,
          enterable: true,
          transitionDuration: 0,
          extraCssText: "z-index:100",
          formatter: function(params, ticket, callback) {
            //根据业务自己拓展要显示的内容
            var res = "";
            var name = params.name;
            var value = params.value[params.seriesIndex + 1];
            res =
              "<span style='color:#fff;'>" +
              name +
              "</span><br/>数据：" +
              value;
            return res;
          }
        },

        visualMap: {
          //图例值控制
          min: 0,
          max: 1,
          calculable: true,
          show: false,
          color: ["#f44336", "#fc9700", "#ffde00", "#ffde00", "#00eaff"],
          textStyle: {
            color: "#fff"
          }
        },
        geo: {
          map: "yunnan",
          zoom: 1.2,
          label: {
            emphasis: {
              show: false
            }
          },
          roam: true, //是否允许缩放
          itemStyle: {
            normal: {
              color: "rgba(51, 69, 89, .5)", //地图背景色
              borderColor: "#516a89", //省市边界线00fcff 516a89
              borderWidth: 1
            },
            emphasis: {
              color: "rgba(37, 43, 61, .5)" //悬浮背景
            }
          }
        },
        series: series
      };
      myChart.setOption(option, true);

      /* end */
    }
  }
};
</script>

<style scoped lang="scss">
.relation-container {
  width: 100%;
  height: 100%;
}
</style>
