<template>
  <div class="pie-echarts-container">
    <div id="pieEcharts" style="width:100%; height:100%"></div>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  created() {},
  mounted() {
    this.drawpieEcharts();
  },
  methods: {
    drawpieEcharts() {
      let myChart = this.$echarts.init(document.getElementById("pieEcharts"));
      let echartData = {
        inner: [
          {
            value: 30,
            name: "报告书"
          },
          {
            value: 113,
            name: "报告表"
          }
        ],
        outer: [
          {
            value: 330,
            name: "报告书"
          },
          {
            value: 113,
            name: "报告表"
          }
        ],
        middle: [
          {
            value: 66,
            name: "报告书"
          },
          {
            value: 113,
            name: "报告表"
          }
        ]
      };

      // 获取图例数据
      var arrName = getArrayValue(echartData.outer, "name");

      function getArrayValue(array, key) {
        var key = key || "value";
        var res = [];
        if (array) {
          array.forEach(function(t) {
            res.push(t[key]);
          });
        }
        return res;
      }

      myChart.setOption(
        {
          title: [
            {
              text: "实际审批项目",
              top: "top",
              left: "right",
              textStyle: {
                fontSize: 20,
                color: "#2e8fe6",
                lineHeight: 26,
                fontWeight: 400
              }
            },
            {
              text: "已上报项目",
              top: "30",
              left: "right",
              textStyle: {
                fontSize: 20,
                color: "#2e8fe6",
                lineHeight: 26,
                fontWeight: 400
              }
            },
            {
              text: "未上报项目",
              top: "60",
              left: "right",
              textStyle: {
                fontSize: 20,
                color: "#2e8fe6",
                lineHeight: 26,
                fontWeight: 400
              }
            }
          ],
          tooltip: {
            trigger: "item",
            formatter: "{a} <br/>{b}: {c} ({d}%)"
          },
          color: ["#5dd286", "#53c9c8", "#489ee5", "#436fce"],
          legend: {
            show: false,
            top: "center",
            left: "80%",
            data: arrName,
            itemWidth: 20,
            itemHeight: 20,
            padding: [0, 5],
            itemGap: 25,
            textStyle: {
              color: "#b0b9cb"
            },
            formatter: function(name) {
              return name + "    " + "1.32";
              // return "{title|" + name + "}\n{value|" + (objData[name].value) + "人}"
            }
          },
          series: [
            //最外层圆环

            {
              name: "实际审批项目",
              type: "pie",
              radius: ["65%", "75%"],
              center: ["40%", "50%"],
              startAngle: "240",
              data: echartData.outer,
              labelLine: {
                normal: {
                  length: 20,
                  length2: 140,
                  lineStyle: {
                    color: "#e6e6e6"
                  }
                }
              },
              itemStyle: {
                // 此配置
                normal: {
                  borderWidth: 2
                  //   borderColor: "#fff"
                },
                emphasis: {
                  borderWidth: 0,
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: "rgba(0, 0, 0, 0.5)"
                }
              },
              label: {
                normal: {
                  position: "inner",
                  formatter: params => {
                    return params.value;
                  },
                  //   fontSize: 22,
                  color: "#fff"
                  // padding: [0, -130, 25, -130],
                }
              }
            },

            {
              name: "已上报项目数",
              type: "pie",
              radius: ["45%", "55%"],
              center: ["40%", "50%"],
              itemStyle: {
                // 此配置
                normal: {
                  borderWidth: 2
                  //   borderColor: "#fff"
                },
                emphasis: {
                  borderWidth: 0,
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: "rgba(0, 0, 0, 0.5)"
                }
              },
              label: {
                normal: {
                  position: "inner",
                  formatter: params => {
                    return params.value;
                  },
                  //   fontSize: 22,
                  color: "#fff"
                }
              },
              labelLine: {
                normal: {
                  show: false
                }
              },
              startAngle: "230",
              data: echartData.inner
            },

            {
              name: "未上报项目数",
              type: "pie",
              radius: ["20%", "30%"],
              center: ["40%", "50%"],
              itemStyle: {
                // 此配置
                normal: {
                  borderWidth: 2
                  //   borderColor: "#fff"
                },
                emphasis: {
                  borderWidth: 0,
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: "rgba(0, 0, 0, 0.5)"
                }
              },
              label: {
                normal: {
                  position: "inner",
                  formatter: params => {
                    return params.value;
                  },
                  //   fontSize: 22,
                  color: "#fff"
                }
              },
              labelLine: {
                normal: {
                  show: false
                }
              },
              startAngle: "230",
              data: echartData.middle
            }
          ]
        },
        true
      );
    }
  }
};
</script>

<style scoped lang="scss">
.pie-echarts-container {
  width: 100%;
  height: 100%;
}
</style>
