<template>
  <div class="type-pie-container">
    <div id="typePie" style="width:100%; height:100%"></div>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  created() {},
  mounted() {
    this.drawtypePie();
  },
  methods: {
    drawtypePie() {
      let myChart = this.$echarts.init(document.getElementById("typePie"));
      var xData = (function() {
        var data = [];
        for (var i = 1; i < 13; i++) {
          data.push(i + "月份");
        }
        return data;
      })();
      myChart.setOption(
        {
          // title: {
          //     text: '天气情况统计',
          //     subtext: '虚构数据',
          //     left: 'center'
          // },
          tooltip: {
            trigger: "item",
            formatter: "{a} <br/>{b} : {c} ({d}%)"
          },
          legend: {
            orient: "vertical",
            // right: 10,
            left: 10,
            top: "center",
            bottom: 20,
            data: ["环评报告书", "环评报告表", "环评登记表"],
            textStyle: {
              color: "#fff"
            }
          },
          color: [
            "#2ec7c9",
            "#b6a2de",
            "#5ab1ef",
            "#ffb980",
            "#d87a80",
            "#8d98b3",
            "#FFEA01",
            "#B8D07C",
            "#fca4bb",
            "#dc69aa",
            "#07a2a4",
            "#9a7fd1",
            "#588dd5",
            "#f5994e",
            "#c05050",
            "#59678c",
            "#c9ab00",
            "#7eb00a",
            "#6f5553",
            "#c14089"
          ],
          series: [
            {
              type: "pie",
              radius: "65%",
              center: ["50%", "50%"],
              selectedMode: "single",
              data: [
                { value: 510, name: "环评报告书" },
                { value: 634, name: "环评报告表" },
                { value: 735, name: "环评登记表" }
              ],
              emphasis: {
                itemStyle: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: "rgba(0, 0, 0, 0.5)"
                }
              }
            }
          ]
        },
        true
      );
    }
  }
};
</script>

<style scoped lang="scss">
.type-pie-container {
  width: 100%;
  height: 100%;
}
</style>
