<template>
  <div class="maps-container">
    <div id="main" style="width:100%; height:100%"></div>
    <div class="btn">
      <span @click="getData(530000)">云南</span>
      <span v-if="cityView">
        <i class="el-icon-arrow-right"></i>
        {{cityName}}
      </span>
    </div>
  </div>
</template>

<script>
import echarts from "echarts";

export default {
  data() {
    return {
      ynCodeList: [
        530000,
        530100,
        530300,
        530400,
        530500,
        530600,
        530700,
        530800,
        530900,
        532300,
        532500,
        532600,
        532800,
        532900,
        533100,
        533300,
        533400
      ],
      cityView: false,
      cityName: "",
      initArr: [],
      taskNames: [],
      areaCode: "",
      titles: ""
    };
  },
  created() {},
  mounted() {
    this.getData(530000);
  },
  methods: {
    getData(cityCode) {
      if (this.ynCodeList.indexOf(cityCode) == -1) {
        /* 地图下钻到市级 */
        console.log("阻止请求");
        return;
      }
      if (cityCode == 530000) {
        this.cityView = false;
      }
      /* 获取城市json数据 */
      import("@/views/echarts/yunnan/" + cityCode + ".geo").then(resData => {
        console.log(resData);
        this.drawMap(resData);
      });
    },

    drawMap(resData) {
      let myChart = echarts.init(document.getElementById("main"));
      echarts.registerMap("yunnan", resData);
      myChart.setOption(
        {
          tooltip: {
            trigger: "item",
            formatter: "{b}"
          },
          geo: {
            map: "yunnan",
            roam: true /* 拖拽 */,
            zoom: 1.2 /* 缩放 */,
            scaleLimit: {
              /* 缩放比例 */
              min: 1,
              max: 2
            },
            label: {
              normal: {
                show: true,
                color: "#fff", // 字的颜色
                fontSize: 14
              },
              emphasis: {
                textStyle: {
                  color: "#fff"
                }
              }
            },
            itemStyle: {
              normal: {
                show: true /* 显示省/市名称 */,
                areaColor: {
                  type: "linear",
                  colorStops: [
                    {
                      offset: 0,
                      color: "#468ebd" // 0% 处的颜色
                    },
                    {
                      offset: 1,
                      color: "#1a5594" // 100% 处的颜色
                    }
                  ]
                },
                borderColor: "#8ae0f1" // 地图边框颜色
              },
              emphasis: {
                borderWidth: 0,
                areaColor: "#389BB7" //鼠标进入时的颜色
              }
            }
          },
          series: []
        },
        true
      );

      myChart.on("click", this.echartsMapClick);
    },
    echartsMapClick(params) {
      /* 地图点击事件 */
      console.log(params);
    }
  }
};
</script>

<style scoped lang="scss">
.maps-container {
  width: 100%;
  height: 100%;
  position: relative;
  .btn {
    position: absolute;
    top: 20%;
    left: 20%;
    border-top: 1px solid aqua;
    border-bottom: 1px solid aqua;
    color: #fff;
    font-size: 20px;
    padding: 6px 0;
    span {
      cursor: pointer;
    }
    span:first-child {
      margin-right: 2px;
    }
  }
}
</style>
