<template>
  <div class="scatter-container">
    <div id="scatterEcharts" style="width:100%; height:100%"></div>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  created() {},
  mounted() {
    this.drawscatterEcharts();
  },
  methods: {
    drawscatterEcharts() {
      let myChart = this.$echarts.init(
        document.getElementById("scatterEcharts")
      );
      // 环评项目个数、登记表项目个数、竣工验收/自主验收项目个数、排污许可项目数
      var plantCap = [
        {
          name: "环评项目",
          value: "1232个"
        },
        {
          name: "登记表",
          value: "324个 "
        },
        {
          name: "排污许可",
          value: "145个 "
        },
        // 	{
        // 	    name: '红嘴鸥',
        // 	    value: 'gull'
        // 	},
        {
          name: "竣工验收",
          value: "457个 "
        },
        {
          name: "自主验收",
          value: "123个"
        }
        // 	{
        // 	    name: '商业街',
        // 	    value: 'street'
        // 	}
      ];

      var datalist = [
        {
          offset: [50, 50],
          symbolSize: 60,
          opacity: 0.95,
          color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
            {
              offset: 0,
              color: "#29c0fb"
            },
            {
              offset: 1,
              color: "#2dc5b9"
            }
          ])
        },
        {
          offset: [38, 70],
          symbolSize: 48,
          opacity: 0.95,
          color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
            {
              offset: 0,
              color: "#35d17e"
            },
            {
              offset: 1,
              color: "#49ddb2"
            }
          ])
        },
        {
          offset: [23, 43],
          symbolSize: 45,
          opacity: 0.95,
          color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
            {
              offset: 0,
              color: "#e5d273"
            },
            {
              offset: 1,
              color: "#e4a37f"
            }
          ])
        },
        {
          offset: [68, 40],
          symbolSize: 45,
          opacity: 0.95,
          color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
            {
              offset: 0,
              color: "#277aec"
            },
            {
              offset: 1,
              color: "#57c5ec"
            }
          ])
        },
        {
          offset: [65, 75],
          symbolSize: 33,
          opacity: 0.68,
          color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
            {
              offset: 0,
              color: "#ff4141"
            },
            {
              offset: 1,
              color: "#ff8989"
            }
          ])
        }
      ];
      var datas = [];
      for (var i = 0; i < plantCap.length; i++) {
        var item = plantCap[i];
        var itemToStyle = datalist[i];
        datas.push({
          name: item.name + "\n" + item.value,
          value: itemToStyle.offset,
          symbolSize: itemToStyle.symbolSize,
          label: {
            normal: {
              textStyle: {
                fontSize: 13,
                lineHeight: 17
              }
            }
          },
          itemStyle: {
            normal: {
              color: itemToStyle.color,
              opacity: itemToStyle.opacity
            }
          }
        });
      }
      myChart.setOption(
        {
          // backgroundColor: '#20203e',
          grid: {
            show: false,
            top: 10,
            bottom: 10
          },
          xAxis: [
            {
              gridIndex: 0,
              type: "value",
              show: false,
              min: 0,
              max: 100,
              nameLocation: "middle",
              nameGap: 5
            }
          ],
          yAxis: [
            {
              gridIndex: 0,
              min: 0,
              show: false,
              max: 100,
              nameLocation: "middle",
              nameGap: 30
            }
          ],
          series: [
            {
              type: "effectScatter",
              // symbol: 'circle',
              // symbolSize: 120,
              symbolSize: function(val) {
                return Math.max(val[2] / 100, 8);
              },
              showEffectOn: "render",
              rippleEffect: {
                brushType: "stroke"
              },
              hoverAnimation: true,
              label: {
                normal: {
                  show: true,
                  formatter: "{b}",
                  color: "#fff",
                  textStyle: {
                    fontSize: "20"
                  }
                }
              },
              itemStyle: {
                normal: {
                  color: "#00acea"
                }
              },
              data: datas
            },
            {
              // name: 'pm2.5',
              type: "effectScatter",
              data: datas,
              symbolSize: function(val) {
                return Math.max(val[2] / 200, 8);
              },
              label: {
                normal: {
                  show: true,
                  formatter: "{b}",
                  color: "#fff",
                  textStyle: {
                    fontSize: "20"
                  }
                }
              },
              itemStyle: {
                normal: {
                  color: "#FF8C00",
                  position: "right",
                  show: true
                }
              }
            }
          ]
        },
        true
      );
    }
  }
};
</script>

<style scoped lang="scss">
.scatter-container {
  width: 100%;
  height: 100%;
}
</style>
